@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@500&display=swap);
html,body{margin:0;padding:0;background-color:#F8F8F8}.content{text-align:center}.content .text{font-size:25px;text-decoration:underline;cursor:pointer}.content .image{height:50vh}.main{display:flex;flex-wrap:wrap;font-family:'Barlow', sans-serif;justify-content:center}.main .question .questionCard{font-size:45px;width:150px;height:100px;display:flex;justify-content:center;align-items:center;padding:10px;text-align:center;border-radius:20px;margin:10px;cursor:pointer;transition:0.2s}.main .question .other{border:7px solid #55E024;color:#55E024}.main .question .other:hover{background-color:#55E024;color:#F8F8F8}.main .question .otherAnswered{background-color:#55E024;color:#F8F8F8}.main .question .image{border:7px solid #FFA235;color:#FFA235}.main .question .image:hover{background-color:#FFA235;color:#F8F8F8}.main .question .imageAnswered{background-color:#FFA235;color:#F8F8F8}.main .question .audio{border:7px solid #35AAFF;color:#35AAFF}.main .question .audio:hover{background-color:#35AAFF;color:#F8F8F8}.main .question .audioAnswered{background-color:#35AAFF;color:#F8F8F8}.main .question .answered{color:#FF3535;border:7px solid #FF3535}.main .question .answered:hover{background-color:#FF3535;color:#F8F8F8}

